body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

a {
  color: inherit;
  text-decoration: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.clearfix:before, .clearfix:after {
  content: '';
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  zoom: 1;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

ul {
  padding: 0;
  margin: 0;
}

.nav-item {
  display: inline-block;
  text-align: center;
  border-right: 1px solid white;
  width: 33%;
}

.nav-item:last-child {
  border-right: none;
}

.nav-item:hover {
  cursor: pointer;
}

.navbar > ul > li > a.active {
  color: gold;
}


@media screen and (max-width:480px) {
  .nav-item {
    font-size: 13px;
    margin-bottom: 2%;
  }
  /* .navbar {
    padding-left: 10%;
  } */
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background: #282828;
  color: #DFDFDF;
  text-align: center;
}

.title {
  margin-bottom: 15px;
}

h1 {
  margin: 0;
  padding: 0;
}

@media screen and (max-width:480px) {
  .header {
    text-align: left;
  }

  .title {
    padding-left: 15%;
  }
}


.story {
  padding-left: 20px;
  padding-right: 5px;
}

.story span {
  padding-left: 5px;
  padding-right: 5px;
  border-right: 1px solid black;
}

.story span:last-child {
  border-right: none;
}

.story-details {
  display: inline;
}

@media screen and (max-width:480px) {
  .story span {
    display: block;
    border-right:none;
  }
}
.story-wrapper {
  padding-bottom: 20px;
}
.pagination {
  cursor: pointer;
  text-align: center;
}
.comment {
  border-bottom: 1px solid black;
  border-left: 1px solid black;
  padding: 10px  20px 10px 20px;
  margin: 10px 0 10px 0;
  overflow: hidden;
  width: calc(100% - 40px);
}

.comment p {
}

.comment-details {
  float: right;
}

.comment-details span {
  padding: 0 5px 0 5px;
  border-right: 1px solid black;
}

.comment-details span:last-child {
  border-right: none;
}

.reply {
  cursor: pointer;
}

